html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

.top_container {
  margin: 16px;
  display: flex;
  justify-content: space-evenly;
}

.img_container {
  background: whitesmoke;
  height: 90dvh;
  margin: 16px;
}

.img_container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  /* opacity: 0.8; */
}

.score {
  font-size: 28px;
  font-weight: bold;
  color: darkgoldenrod;
}

button {
  width: 100px;
  height: 32px;
  font-size: 14px;
}
